/**
 * Wait.
 * @returns {Promise} The sum of the two numbers.
 */
function wait(): Promise<any> {
  return new Promise(resolve => {
    setTimeout(() => {
      console.log('wait');
    }, 2000);
  });
}


/**
 * Wait.
 * @returns {Void} none return
 */
async function main() {
  await wait();
  await wait();
  await wait();
}

main();
